import React from "react";
import Layout from "./Layout";
import { graphql, Link } from "gatsby";
import { SEO } from "theme-seo";
import GetPersianDate from "./JalaliDate";
import { GetBackendUrl } from "theme-library";

const Factor = ({ data, pageContext }) => {
  let senfId;
  if(typeof window !== "undefined" ){
    const url = new URL(window.location);
    senfId = url.searchParams.get("senfId");
  }
  
  let parseServerUrl = pageContext.parseServerUrl;
  const backendUrl = GetBackendUrl();
  if (backendUrl !== null) parseServerUrl = backendUrl;
  const [plan, setPlan] = React.useState({
    IsSelectedOption1: false,
    IsSelectedOption2: false,
    Option1Count: 0,
    Option2Count: false,
  });
  const [result, setResult] = React.useState({ Total: "" });
  const [options, setOptions] = React.useState([]);

  const claims = getCookie("Claims");
  const user = claims ? JSON.parse(decodeURIComponent(claims)) : null;

  React.useEffect(function () {
    fetch(parseServerUrl + "parse/classes/PlanOption", {
      headers: {
        "X-Parse-Application-Id": `${pageContext.applicationId}`,
      },
    })
      .then(function (response) {
        return response.json();
      })
      .then(function (r) {
        const opts = r.results.map((option) => {
          option.IsSelected = option.IsMulti ? true : false;
          option.Count = 1;
          return option;
        });
        opts.sort(function (a, b) {
          return a.Order - b.Order;
        });
        setOptions(opts);
      });
  }, []);

  React.useEffect(
    function () {
      let selectedPlan = window.localStorage.getItem("selectedPlan");
      let selectedPlanObject = JSON.parse(selectedPlan);
      if (selectedPlanObject) {
        setPlan(selectedPlanObject);
        window.localStorage.removeItem("selectedPlan");
        CalculateTotalPrice(selectedPlanObject);
      } else {
        let newPlan = pageContext.plan;
        newPlan.TotalTax = (newPlan.DiscountedPrice * newPlan.Tax) / 100;
        setPlan(newPlan);
        CalculateTotalPrice(newPlan);
      }
    },
    [options]
  );

  function NumberWithCommas(x) {
    return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
  }

  const [buttonState, setButtonState] = React.useState({
    state: true,
    buttonStyle: "cursor-not-allowed",
  });

  function CalculateTotalPrice(p) {
    const invoice = { PlanId: p.objectId };
    invoice.SenfId = senfId;
    invoice["Options"] = options
      .filter((m) => m.IsSelected)
      .map((m) => (m.IsMulti ? m.objectId + ":" + m.Count : m.objectId))
      .join();
    console.log(invoice);
    fetch(parseServerUrl + "parse/functions/calc", {
      method: "POST",
      headers: {
        "X-Parse-Application-Id": `${pageContext.applicationId}`,
        "Content-Type": "application/json",
      },
      body: JSON.stringify({ invoice: invoice }),
    })
      .then(function (response) {
        return response.json();
      })
      .then(function (response) {
        console.log(response.result);
        setResult(response.result);
      });
  }

  function SelectOption(event) {
    let checked = event.target.checked;
    let compyOptions = [...options];
    compyOptions = compyOptions.map((option) => {
      if (option.Title === event.target.name) {
        option.IsSelected = checked;
        option.Count = 1;
      }
      return option;
    });

    setOptions(compyOptions);
    let newPlan = { ...plan };
    newPlan["Options"] = compyOptions;
    CalculateTotalPrice(newPlan);
  }

  function SetOptionCounts(event) {
    let newOptions = [...options];
    newOptions = newOptions.map((option) => {
      if (option.objectId === event.target.name) {
        option.Count = event.target.value;
        option.IsSelected = option.Count > 0;
      }

      return option;
    });

    setOptions(newOptions);
    let newPlan = { ...plan };
    newPlan["Options"] = newOptions;
    CalculateTotalPrice(newPlan);
  }

  function EnableButton(event) {
    let bState = { state: !event.target.checked };
    if (bState.state) {
      bState.buttonStyle = "cursor-not-allowed";
    } else {
      bState.buttonStyle = "opacity-100 hover:bg-green-600";
    }

    setButtonState(bState);
  }

  function CreateSelectItems() {
    let items = [];
    for (let i = 1; i <= 10; i++) {
      items.push(
        <option key={i} value={i}>
          {i}
        </option>
      );
    }
    return items;
  }

  function getCookie(name) {
    if (typeof document === "undefined") return;
    const value = `; ${document.cookie}`;
    const parts = value.split(`; ${name}=`);
    if (parts.length === 2) return parts.pop().split(";").shift();
  }

  function SetProductInLocalStorage() {
    window.localStorage.setItem("selectedPlan", JSON.stringify(plan));
  }

  function CreateInvoice() {
    if (!user) {
      SetProductInLocalStorage();
      window.location = pageContext.authUrl + "?returnUrl=" + window.location;
      return;
    }

    if (
      (user && !user.CodeEghtesadi && !user.CodeMelli) ||
      (user &&
        user.CodeMelli &&
        (!user.Ostan ||
          !user.Shahr ||
          !user.Senf ||
          !user.Name ||
          !user.FamilyName ||
          !user.MobileNumber ||
          !user.NameSherkat)) ||
      (user &&
        !user.CodeMelli &&
        user.CodeEghtesadi &&
        (!user.ShenaseMelli ||
          !user.Address ||
          !user.CodePosti ||
          !user.NameSherkat ||
          !user.Name ||
          !user.FamilyName ||
          !user.MobileNumber))
    ) {
      SetProductInLocalStorage();
      window.location = window.location.origin + "/profile?returnUrl=" + window.location;
      return;
    }

    function SaveInvoice(shamsiDate) {
      const date = new Date().toISOString().slice(0, 10);
      let invoice = {
        PlanId: plan.objectId,
        SenfId: senfId,
        Tarikh: shamsiDate,
        ProductName: plan.Name,
        UserID: user.ParseUser,
        ChandSandooghe: options.filter((o) => o.Name === "TedadSandoogh")[0].Count > 1,
        Date: date,
        ACL: {
          [user.ParseUser]: {
            read: true,
            write: true,
          },
        },
      };

      invoice.Options = options
        .filter((m) => m.IsSelected)
        .map((m) => (m.IsMulti ? m.objectId + ":" + m.Count : m.objectId))
        .join();

      fetch(parseServerUrl + "parse/classes/Invoice", {
        method: "POST",
        headers: {
          "X-Parse-Application-Id": `${pageContext.applicationId}`,
          "Content-Type": "application/json",
        },
        body: JSON.stringify(invoice),
      })
        .then(function (response) {
          return response.json();
        })
        .then(function (result) {
          const validUsers = ["G10w7XwAXF", "5uJhGYbQUs", "BhQk7FiTC6"];
          const amount =
            window.location.hostname === "gatsby.raymasoft.com" ||
            window.location.hostname === "gatsby.rayvarz.studio" ||
            window.location.hostname === "test.rayvarz.studio" ||
            window.location.hostname === "latest.raymasoft.com" ||
            window.location.hostname === "localhost" ||
            validUsers.includes(user.ParseUser)
              ? "100000"
              : result["Amount"] * 10;
          const redirectUrl = window.location.origin + "/profile";
          const invoiceId = pageContext.parseServerPrefix
            ? pageContext.parseServerPrefix + ":" + result["objectId"]
            : result["objectId"];
          fetch(
            pageContext.paymentServerUrl +
              "Pay?redirectUrl=" +
              redirectUrl +
              "&amount=" +
              amount +
              "&invoiceId=" +
              invoiceId +
              "&flags=GenerateLicense,EnableLicense"
          )
            .then(function (response) {
              return response.json();
            })
            .then(function (response) {
              document.getElementById("amount").value = amount;
              document.getElementById("reserveNum").value = invoiceId;
              document.getElementById("redirectUrl").value = redirectUrl;
              document.getElementById("token").value = response["token"];
              document.getElementById("form").submit();
            });
        });
    }

    const persianDate = GetPersianDate(new Date());
    SaveInvoice(persianDate);
  }

  return (
    <Layout>
      <SEO title={pageContext.title} />
      {plan.Name && (
        <div className="my-8 p-2 w-11/12 lg:w-3/4 mx-auto">
          <div className="lg:flex" key={plan.Name}>
            {plan.Image ? (
              <img
                className={"m-8 mr-0 h-48"}
                width="350"
                height="150"
                src={plan.Image}
                alt={plan.Name}
              />
            ) : null}
            <table
              key={plan.Name}
              className="mx-auto text-right border-collapse text-gray-700 lg:w-2/3"
            >
              <thead>
                <tr className="py-4 px-6 border-b-4 border-teal-500">
                  <th>مشخصات محصول</th>
                  <th>قیمت(تومان)</th>
                </tr>
              </thead>
              <tbody>
                <tr className="py-4 px-6 border-b border-gray-300">
                  <td>{plan.Name}</td>
                  {result && result.BasePrice && (
                    <td className="text-center">{NumberWithCommas(result.BasePrice)}</td>
                  )}
                </tr>
                {options && options.length > 0 && (
                  <tr className="py-4 px-6 border-b border-gray-300">
                    <div className="flex flex-col">
                      {options
                        .filter((opt) => !opt.PlanId || parseInt(opt.PlanId) <= parseInt(plan.Id))
                        .map((option) =>
                          option.IsMulti ? (
                            <>
                              <td className="">
                                <label className="mr-1 align-top">{option.Title}</label>
                                <select
                                  type="number"
                                  className="border-2 border-gray-400 w-12 mr-8 ml-2"
                                  name={option.objectId}
                                  value={option.Count}
                                  onChange={SetOptionCounts}
                                >
                                  {CreateSelectItems()}
                                </select>
                              </td>
                            </>
                          ) : (
                            <td className={""}>
                              <input
                                type="checkbox"
                                key={option.objectId}
                                checked={option.IsSelected}
                                name={option.Title}
                                onChange={SelectOption}
                                className="regular-checkbox"
                              />
                              <label className="mr-1 align-top">{option.Title}</label>
                              <label className="mr-1 align-top font-bold">
                                {" "}
                                ({NumberWithCommas(option.Price)} تومان)
                              </label>
                            </td>
                          )
                        )}
                    </div>
                    <td className="text-center">
                      {" "}
                      {result && result.OptionsTotal && NumberWithCommas(result.OptionsTotal)}{" "}
                    </td>
                  </tr>
                )}
              </tbody>

              {result && result.Total && (
                <tfoot className="my-6">
                  <tr className="py-4 px-6 border-b border-gray-300">
                    <td>مجموع (با احتساب مالیات بر ارزش افزوده)</td>
                    <td className="font-bold text-center">{NumberWithCommas(result.Total)}</td>
                  </tr>
                </tfoot>
              )}
            </table>
          </div>

          <form id="form" action="https://pna.shaparak.ir/_ipgw_/payment/" method="post">
            <input type="hidden" id="token" name="token" />
            <input type="hidden" id="language" name="language" value="fa" />
            <input type="hidden" id="transType" name="transType" value="enGoods" />
            <input type="hidden" id="reserveNum" name="reserveNum" />
            <input type="hidden" id="amount" name="amount" />
            <input type="hidden" id="redirectUrl" name="redirectUrl" />
          </form>

          <div className="my-10">
            <div className="lg:mr-48">
              <input
                type="checkbox"
                name="licence"
                onChange={EnableButton}
                className="regular-checkbox"
              />
              <label className="mr-1 align-top">
                تمامی بند های{" "}
                <Link to="/legal" className="text-teal-600 font-bold">
                  قوانین و مقررات
                </Link>{" "}
                را قبول دارم.
              </label>
            </div>
            <div>
              <button
                disabled={buttonState.state}
                className={
                  "bg-teal-600 text-white opacity-50 font-bold py-2 px-4 rounded-lg my-10 float-left " +
                  buttonState.buttonStyle
                }
                type="button"
                onClick={CreateInvoice}
              >
                ادامه فرایند خرید
              </button>
            </div>
          </div>
        </div>
      )}
    </Layout>
  );
};

export default Factor;
