import jalaliday from "jalaliday";
import dayjs from "dayjs";

dayjs.extend(jalaliday);
dayjs.calendar("jalali");

var symbolMap = {
  1: "۱",
  2: "۲",
  3: "۳",
  4: "۴",
  5: "۵",
  6: "۶",
  7: "۷",
  8: "۸",
  9: "۹",
  0: "۰",
};

export default function GetPersianDate(input) {
  return dayjs(input)
    .locale("fa")
    .format("dddd، DD MMMM YYYY")
    .replace(/\d/g, (match) => symbolMap[match])
    .replace(/,/g, "،");
}
